import ButtonDota from "@/components/utils/cards/ButtonDota";
import CardDota from "@/components/utils/cards/Card";
import useAxios from "@/hooks/useAxios";
import axiosInstance from "@/services";
import { MyRank, Rank, RanksMap } from "@/types/Rank";
import { User } from "@/types/User";
import { Progress } from "@nextui-org/react";
import { useEffect, useState } from "react";

const NextRank = () => {
  const [rank, setRank] = useState<MyRank | null>(null)
  const [nextRank, setNextRank] = useState<Rank | null>(null)
  const { data: user } = useAxios<User>({ url: 'users/me', method: 'get' })
  const { data: ranks } = useAxios<RanksMap>({ url: 'ranks/list', method: 'get' })
  const getUserRank = async () => {
    try {
      if (user) {
       
        const response = await axiosInstance.post('ranks/getUserRank', {
          id_user: user.id
        })
        if (response.status == 200 || response.status == 201) {
          setRank(response.data as MyRank)
        }
      }

    } catch (error) {
      console.error("fallo al obtener el rango", error)
    }
  }

  const calculateNextRank = () => {
    if(!rank || !ranks) return
    const current_rank = ranks[rank.rank]
    if(!current_rank) return
    const nextRank =  Object.values(ranks).find((r) => r.order == current_rank.order + 1)
    setNextRank(nextRank || null)
  }

  useEffect(() => {
    getUserRank()
  }, [user])

  useEffect(() => {
    calculateNextRank()
  }, [rank, ranks])

  return (
    <CardDota classNames="flex flex-col gap-1 items-center">
      <div className="flex flex-col gap-4">
        <span className="text-3xl font-bold">Plan Carrera</span>
        <ButtonDota rounded="lg">
          <span className="font-bold">VER RANGOS</span>
        </ButtonDota>
      </div>
      <div className="flex justify-between items-center w-full">
        <div className="flex flex-col gap-2 items-center">
          <img src="/img/0.png" className="w-[100px] rounded-full" alt="" />
          <span className="text-[12px]">Rango Actual ({rank?.points || 0})</span>
          <span className="text-xl">{rank?.display || 'Sin rango'}</span>
        </div>
        <div className="flex flex-col gap-2 items-center">
          <img src="/img/0.png" className="w-[100px] rounded-full" alt="" />
          <span className="text-[12px]">Próximo rango ({nextRank?.points || 0})</span>
          <span className="text-xl">{nextRank?.display || "Sin rango"}</span>
        </div>
      </div>
      <Progress value={(100 / (nextRank?.points || 0)) * (rank?.points || 0)} />
    </CardDota>
  );
};

export default NextRank;
