import CardDota from "@/components/utils/cards/Card";
import useAxios from "@/hooks/useAxios";
import axiosInstance from "@/services";
import { User } from "@/types/User";
import { useEffect, useState } from "react";

const RankDashboard = () => {
  const [rank, setRank] = useState(null)
  const { data: user } = useAxios<User>({ url: 'users/me', method: 'get' })
  const getUserRank = async () => {
    try {
      if (user) {
       
        const response = await axiosInstance.post('ranks/getUserRank', {
          id_user: user.id
        })
        console.log("status", response)
        if (response.status == 200 || response.status == 201) {
          setRank(response.data)
        }
      }

    } catch (error) {
      console.error("fallo al obtener el rango", error)
    }
  }

  useEffect(() => {
    getUserRank()
  }, [user])


  return (
    <CardDota classNames="flex flex-col gap-8">
      <div className="flex justify-between items-center">
        <div className="flex flex-col items-center">
          <img src="/img/0.png" className="w-[100px] rounded-lg" alt="" />
          <span className="bg-primary text-white font-bold px-2 py-1 rounded-lg">
            RANGO
          </span>
        </div>
        <div className="flex flex-col gap-1 items-end">
          <span>Contador de usuarios</span>
          <span className="text-lg">138</span>
        </div>
      </div>
      <div className="flex justify-between">
        <div className="flex gap-8 items-center justify-center">
          <img src="/img/0.png" className="rounded-full w-[60px]" alt="" />
          <div className="flex flex-col gap-1">
            <span className="bg-primary text-white font-bold px-2 py-1 rounded-lg">
              CALIFICADO
            </span>
            <span className="text-xl">DotaMaster</span>
            <span className="">ID 10000</span>
          </div>
        </div>
        <div className="flex flex-col items-end justify-center">
          <span>Balance historico</span>
          <span className="text-xl">$ 75.00 USD</span>
        </div>
      </div>
    </CardDota>
  );
};

export default RankDashboard;
